body {
  font-family: Alexandria !important;
}
.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #191b1f;  
  direction: ltr;
}

/* Custom Font */


@font-face {
  font-family: Alexandria;
  src: url(/public/fonts/Alexandria-ExtraLight.ttf);
  font-weight: 200;
}
@font-face {
  font-family: Alexandria;
  src: url(/public/fonts/Alexandria-Regular.ttf);
  font-weight: normal;
}
@font-face {
  font-family: Alexandria;
  src: url(/public/fonts/Alexandria-Bold.ttf);
  font-weight: bold;
}
@font-face {
  font-family: Alexandria;
  src: url(/public/fonts/Alexandria-Black.ttf);
  font-weight: 900;
}
/* ====Custom Font */

.iconButton {
  transition: all 0.2s !important;
}

.iconButton:hover {
  background-color: #c5c5c5 !important;
  box-shadow: 0px 7px 7px rgba(0, 0, 0, 0.4);
}

.todoCard {
  transition: all 0.2s !important;
}

.todoCard:hover {
  padding-top: 20px;
  padding-bottom: 20px;
  box-shadow: 0px 7px 7px rgba(0, 0, 0, 0.4);
}